import { FC, useMemo } from 'react';
import classNames from 'classnames';

// Styles
import './RadioButtons.scss';

// Types
import { TestMetadata } from 'types';

export interface RadioButtonsProps {
  /* Determine whether radio buttons should be displayed horizontally */
  horizontal?: boolean;
  /* Custom class name(s) for component container */
  className?: string;
  /** Display name for field */
  labelText?: string;
  /** Boolean to determine if control is required */
  required?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const RadioButtons: FC<RadioButtonsProps> = ({
  className,
  children,
  horizontal = false,
  labelText,
  required,
  testMetadata,
  ...otherProps
}) => {
  const radioButtonsClassNames = useMemo(
    () =>
      classNames(
        'sb-radio-buttons',
        {
          'sb-radio-buttons-horizontal': horizontal,
        },
        className,
      ),
    [className, horizontal],
  );

  return (
    <ul {...otherProps} className={radioButtonsClassNames} {...testMetadata}>
      {labelText && (
        <label
          className={`sb-form-control__label ${required && 'is-required'}`}
        >
          {labelText}
        </label>
      )}
      {children}
    </ul>
  );
};

export default RadioButtons;
