import { ComponentProps, FC, HTMLProps, useCallback } from 'react';
import classNames from 'classnames';

// Styles
import './Radio.scss';

// Types
import { CheckOption, FormOptionOnChange, TestMetadata } from '../types';

export interface RadioProps {
  onChange?: FormOptionOnChange;
  option: CheckOption;
  checked?: boolean;
  listItemClassName?: string;
  className?: string;
  /** Additional input props for Input, based on the inputComponent */
  inputProps?: HTMLProps<HTMLInputElement>;
  labelProps?: ComponentProps<'label'> & TestMetadata;
  isError?: boolean;
  // Test IDs for component sections
  testMetadata?: TestMetadata;
}

const Radio: FC<RadioProps> = ({
  option,
  onChange = () => {
    // noop
  },
  checked,
  inputProps,
  listItemClassName = '',
  className = '',
  testMetadata,
  labelProps,
  isError = false,
  ...otherProps
}) => {
  const memoizedOnChange = useCallback(
    () => onChange(option.value),
    [onChange, option.value],
  );

  return (
    <li
      className={classNames('sb-radio__list-item', listItemClassName)}
      {...otherProps}
    >
      <div
        className={classNames(
          'sb-radio',
          { 'sb-radio_error': isError },
          className,
        )}
      >
        <label htmlFor={String(option.id)} className="sb-radio__label">
          <input
            className="sb-radio__input"
            type="radio"
            name={String(option.name || option.id)}
            id={String(option.id)}
            checked={checked}
            onChange={memoizedOnChange}
            {...testMetadata}
            {...inputProps}
          />
          <div>
            <div
              className={classNames(
                'sb-radio__label-text',
                labelProps?.className,
              )}
            >
              {option.label}
            </div>
            {option.helpText && (
              <div className="sb-radio__helper-text">{option.helpText}</div>
            )}
          </div>
        </label>
      </div>
    </li>
  );
};

export default Radio;
